                  <div class="txt">
                    <img src="http://210.38.139.34:8082/uploadImages/1547038900137_996545.png">
                     <p> 我校最早是在电子工程本科专业2002级人才培养方案中设立《EDA技术与应用》课程，在2004年开始授课。由于历史的原因，以及对EDA技术认识的局限性，当时的授课内容与现代EDA技术思想相差较大，主要以电路板绘制为主要内容。

                        2004年软件工程专业开始招生，在制定人才培养方案时将EDA技术、嵌入式操作系统、ARM处理器和基于FPGA开发作为嵌入式系统专业方向的主干课程，鉴于与电子工程等专业的《EDA技术与应用》课程内容差异太大，同时突出软件工程专业对该课程要求的特点，将该课程取名为《VHDL语言程序设计》，经过四年的课程建设，于2008年将《VHDL语言程序设计》课程建设成为广东海洋大学校级精品课。

                          原信息学院在制定2009级本科人才培养方案时，对学院内包括软件工程专业、电子工程专业在内的各专业EDA技术课程的内容和课程名称进行了整合，统一命名为《EDA技术与应用》，课程内容和组织形式与原《VHDL语言程序设计》课程一致。经教务处批准，从2011年6月起，将原校级精品课程《VHDL语言程序设计》课程更名为《EDA技术与应用》。

