                  <div class="txt">
                     <p> 课程建设情况简介（教学内容的先进性，运用现代信息技术情况，教学改革情况，理论教学与实践教学结合情况，课程特色，教学效果，教学组织管理等）:

                                1.课程及其教学内容的先进性
                                硬件描述语言是在微电子技术、可编程逻辑器件PLD和电子设计自动化技术DEA等前沿学科高速发展的大背景下应用而生的，其内容本身是非常先进的。而VHDL以其强大的系统描述能力、规范的程序设计结构、灵活的语句表达风格和多层次的仿真测试手段，受到业界普遍的认同和广泛的接受，从数十种国际流行的 硬件描述语言中脱颖而出，成为EDA领域首选的硬件描述语言，并已经成为IEEE的硬件描述语言标准。因此，我们选择VHDL作为硬件描述语言的教学内容具有先进性。教学内容上我们首先从选择国际上最先进的，由集成电路制造业的领导者原 Altera公司（现intel全资子公司）研发的QuartusⅡ集成开发平台着手，进行教材的组织，教学内容的筛选，实验教学内容的组织，实验设备的引进和实验室的建设。为此，我们加入了intel的大学计划，并建立了“可编程逻辑与异构计算联合实验室”，走出了一条校企联合，用少量的资金建立高水平实验室，实现资源共享的道路，为提高教学质量奠定了物质基础。更重要的是，通过这种方式选择的教学内容保证了其先进性、实用性，避免了理论与工程实际脱节，提高了学生的学习效率。

                                2.教学改革情况
                                课程体系的改革。教学过程中的每一门课程都不应该是独立的，都应该是相互依存为达到教学目标服务的。《EDA技术与应用》也不例外，它必须与《计算系统基础》、《计算机组成与结构》、《嵌入式系统》等课程构成课程群，达到培养嵌入式系统软件开发人才的目标。

                                教学模式和教学方法的改革。首先，本课程在教学模式和教学方法的改革放置在专业建设与改革中进行，并围绕本课程负责人主持的“软件工程专业实践教学体系的探索与实施”、“地方高校软件工程专业卓越人才培养的研究与实施”两个广东省质量工程项目展开，将本课程由传统的理论教学为主，转变为理论教学与工程实践相结合，以工程实践为主的教学模式，在剖析工程案例的过程中学习理论，反过来，再以理论指导工程实践，并在教学实践中总结出了“学习、模仿、创新三步教学法”。结合软件工程卓越人才培养，提高学生的学习效率，创新性地提出并实施了理论实践融合教学模式，缩短了理论教学与实践教学在时间和空间上的距离，这种教学模式受到学生欢迎，取得了良好的教学效果。“软件工程专业实践教学体系与三步教学法的研究”、“理论实践融合教学模式在软件工程卓越人才培养中的研究与实践”2项研究成果获得广东海洋大学教学成果一等奖。

                                考核方式的改革。为了适应应用型人才培养的要求，自2005年起本课程已将传统的以考查理论知识水平为目的的闭卷考试方法，变为以考查学生的工程实践能力为主，以理论水平考查为辅，资源全开放式的考核方式，具体形式是考核学生完成嵌入式系统设计和实现的能力和水平。

                                3.课程特色
                                《EDA技术与应用》这门课程在课程群中起着承上启下的重要作用，同时也是一门实践性很强的课程，经过十多年的探索与实践已经形成以下特色：

                                  采用理论实践融合教学模式，缩短了理论教学与实践教学在时间和空间上的距离，提高了学生的学习效率，在教学模式上具有特色。

                                  在教学过程中广泛采用“学习、模仿、创新三步教学法”，切实有利于学生工程实践能力和创新能力的培养，在教学方法上具有特色。

                                  在考核方式上，采用考核学生工程实践能力为主，考核知识理论水平为辅的考核方式，适应了应用型、创新型人才培养的要求，在考核方式上具有特色。

