                  <!-- <div class="xq_tit bdb_d">
                    <h1>
                      教学方法
                    </h1>
                    <p>
                      <span>作者：张老师</span
                      <span>发布时间：2021-3-21 20:19:56</span>
                    </p>
                  </div>
                  <div class="txt">
                     <p> 一、教学方法的改革
                        本课程采用理论紧密联系实践的方法，根据各教学环节的教学目的，多种教学方法灵活使用。

                        理论课采用案例教学的方法，以讲授为主，辅之以课堂讨论。在硬件描述语言的讲授中，将语法知识融入实际案例中，通过波形分析帮助同学理解，同时注重对比与总结分析，将知识点串起来，方便学生记忆与掌握。在设计方法的讲授中，主要是培养学生的创新能力，课堂上大胆采用启发式教学方法，鼓励学生去尝试，并通过学生自己发现问题，自己解决问题达到教学目的。

                        实验课教学中，全部内容围绕提高学生的工程实践能力和培养其创新能力展开，采用课内与课外结合的方式，课外学生必须完成设计和仿真验证，课内在教师的指导下解决出现的问题，借助实验室的软硬件资源进行硬件测试。为此，精心设计了设计性实验指导书，一个实验分若干个小的实验项目，但各个小项目是层层递进的关系，充分体现自顶向下设计和自底向上实现的原则，使实验水平上升到一个较高高度。

                        在此基础上，我们创新性地提出并实施了“学习、模仿、创新三步教学法”，这一教学方法符合教育教学的规律，取得了非常好的教学效果，得到广泛认可，教学成果获得广东海洋大学教学成果一等奖。

                        二、考核方式的改革
                        考虑到课程实践性强、设计性强的特点，加大实验教学的考核比例，由原来的20%加大到60%，重点考核学生的设计思路、实现能力及调试能力。

                        笔试采用闭卷的方式，占40%，以设计性题目为主，注重考察学生的知识运用能力而不是语法的死记硬背，重在理解而不是默写。

                        通过考核方式改革，可以加强学生对课程内容的深刻理解和灵活应用，将目标直接指向应用型人才培养。</p>
                  </div> -->
                  <div class="title">
                    <span>{{ pageMsg[3].title }}</span>
