<script>
import axios from "axios";

export default {
  data() {
    return {
      currpage: "课程简介",
      menu: ["课程简介", "历史沿革", "建设规划", "教学方法"],
      article: {},
      list: [],
      page: {},
      pageMsg: [
        { title: "", time: "", author: "", content: "" },
        { title: "", time: "", author: "", content: "" },
        { title: "", time: "", author: "", content: "" },
        { title: "", time: "", author: "", content: "" },
      ],
    };
  },
  created() {
    this.loadData("课程简介");
    for (let i of [1, 2, 3, 4]) {
      axios
        .post("/api/index/intro/get", { id: i })
        .then((res) => {
          console.log(res);
          this.pageMsg[i - 1].title = res.data.data.title;
          this.pageMsg[i - 1].time = res.data.data.createTime;
          this.pageMsg[i - 1].content = res.data.data.content;
          this.pageMsg[i - 1].author = res.data.data.author;
          return;
        })
        .catch((err) => {
          console.log(err);
        });
    }
  },
  methods: {
    loadData: function (page) {
      this.currpage = page;
    },
  },
  watch: {
    $route(to, from) {
      this.loadData(this.menu[to.query.index]);
    },
  },
};
</script>
<style scoped>
.zy_newsxq {
  display: flex;
  flex-direction: column;
  align-content: center;
}
.title {
