  text-align: center;
  padding: 20px 0 0 0;
}
.otherMsg {
  height: auto;
  width: 100%;
  display: flex;
  justify-content: center;
  color: #999;
  margin: 0 0 10px 0;
  padding: 0 0 20px 0;
  border-bottom: 1px solid #ddd;
}
.otherMsg span {
  margin: 0 20px 0 0;
}
</style>



